import React, { FC } from "react";
import { SubmitHandler, useForm } from "react-hook-form";
import { useNavigate } from "react-router-dom";
import { useLoginUserMutation } from "../../../store/RTK Query/loginApi";
import { Box, Button, TextField, Typography } from "@mui/material";

interface LoginFormProps {
  onSwitch: () => void;
}

interface LoginFormIputs {
  username: string;
  password: string;
}

const LoginForm: FC<LoginFormProps> = ({ onSwitch }) => {
  const navigate = useNavigate();

  const [loginUser, { error, isLoading }] = useLoginUserMutation();

  const {
    register,
    formState: { errors, isValid },
    handleSubmit,
    reset,
  } = useForm<LoginFormIputs>({ mode: "onSubmit" });

  const onSubmit: SubmitHandler<LoginFormIputs> = async (data) => {
    try {
      const response: { token: string } = await loginUser(data).unwrap();
      navigate("/chat");
      return response.token;
    } catch (error) {
      alert(`  : ${JSON.stringify(error)} `);
    }
  };

  return (
    <Box
      component="form"
      onSubmit={handleSubmit(onSubmit)}
      sx={{
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        gap: 1,
        padding: 2,
        height: "100%",
      }}
    >
      <Typography variant="h4"> </Typography>

      <TextField
        id="standard-basic"
        label="Login"
        variant="outlined"
        size="small"
        {...register("username", {
          required: "   !",
          minLength: { value: 5, message: " 5 ." },
        })}
        error={!!errors.username}
        helperText={errors?.username?.message}
      />

      <TextField
        id="outlined-basic"
        label="Password"
        variant="outlined"
        type="password"
        size="small"
        {...register("password", {
          required: "   !",
          minLength: { value: 6, message: " 6 ." },
        })}
        error={!!errors.password}
        helperText={errors?.password?.message}
      />

      <Button
        type="submit"
        sx={{
          margin: 1,
          bgcolor: "darkgrey  ",
          color: "white",
          width: "50%",
        }}
        disabled={!isValid}
        size="small"
      >
        
      </Button>

      <Box
        sx={{
          display: "flex",
          justifyContent: "space-between",
          width: "100%",
          mt: "auto",
        }}
      >
        <Button
          variant="text"
          onClick={() => {
            alert("  !");
          }}
          sx={{ fontSize: "12px" }}
        >
           ?
        </Button>

        <Button variant="text" onClick={onSwitch} sx={{ fontSize: "12px" }}>
          
        </Button>
      </Box>
    </Box>
  );
};
export default LoginForm;
